.statistics {
	width: 320px;
    margin: 15px auto;
    border: solid 1px rgb(211, 221, 221);
}

.title {
	font-size: 16p;
	color: #333333;
	text-align: center;
    margin-top: 30px;
    margin-bottom: 25px;
}

.stat_list{
    height: 60px;
    display: flex;
    list-style: none;
}

.item {
    width: 70px;
    border: solid 1px teal;
}

.label{
    margin-top: 5px;
    margin-bottom: 5px;
    display: flex;
    justify-content: space-evenly;
}

.percentage{
    font-size: 16px;
    display: flex;
    justify-content: space-evenly;
}

/*  */
.item:nth-child(odd){
    background-color: aqua;
}

.item:nth-child(even){
    background-color: sandybrown;
}